export function toNumber(value) {
	// V√©rifie si la valeur est d√©j√† un nombre
	if (typeof value === "number" && !isNaN(value)) {
		return value;
	}

	// Tente de convertir en nombre si c'est une cha√Æne de caract√®res
	const convertedValue = Number(value);
	if (!isNaN(convertedValue)) {
		return convertedValue;
	}

	// Retourne null si la conversion a √©chou√©
	return null;
}
